.grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 20px;

  @include tablet {
    grid-template-columns: repeat(6, 1fr);
    column-gap: 30px;
  }

  @include small-desktop {
    grid-template-columns: repeat(12, 1fr);
    column-gap: 20px;
  }

  @include small-desktop {
    column-gap: 30px;
  }

  &--desktop {
    display: block;

    @include tablet {
      display: grid;
    }

    @include small-desktop {
      display: grid;
    }
  }

  &__item {
    @for $start from 1 through 12 {
      @for $end from $start through 12 {
        &--tablet-#{$start}-#{$end} {
          @include tablet {
            grid-column: #{$start} / #{$end + 1};
          }
        }

        &--desktop-#{$start}-#{$end} {
          @include small-desktop {
            grid-column: #{$start} / #{$end + 1};
          }
        }

        &--large-#{$start}-#{$end} {
          @include small-desktop {
            grid-column: #{$start} / #{$end + 1};
          }
        }
      }
    }
  }
}
